}
}else if(this.info[key]){
return true;
}








},
getBirthdayFromIdCard(idCard){
var birthday = "";
if(idCard != null && idCard != ""){
if(idCard.length == 15){
birthday = "19"+idCard.substr(6,6);
} else if(idCard.length == 18){
birthday = idCard.substr(6,8);
}

birthday = birthday.replace(/(.{4})(.{2})/,"$1-$2-");
}
return birthday;
},
regMobile(key){
if(this.info[key]){
if(!this.reg.phone.test(this.info[key])){
this.showToast = true;
this.toastMsg = '';
return false;
}
return true;
}
},
getAge(str){
var r = str.match(/^(\d{1,4})(-|\/)(\d{1,2})\2(\d{1,2})$/);
if(r==null)return   false;
var d= new Date(r[1], r[3]-1, r[4]);
if(d.getFullYear()==r[1]&&(d.getMonth()+1)==r[3]&&d.getDate()==r[4]){
var Y = new Date().getFullYear();
return Y-r[1];
}
return '';
},
isLeap(year){
return year%100==0?(year%400==0?1:0):(year%4==0?1:0);
},
getPregnancyBirth(date){
var monthDays = new Array(31,28+this.isLeap(nextYear),31,30,31,30,31,31,30,31,30,31);
var rq = new Date(date),
nowYear = rq.getFullYear(),
nowMonth = rq.getMonth()+1,
nowDate = rq.getDate(),
nowDay = rq.getDay(),
nextMonth = nowMonth,
nextYear = nowYear,
nextDate = nowDate;
if(nowMonth+9 > 12){
nextMonth-=3;
nextYear+=1;
}else{
nextMonth+=9;
nextYear = nowYear;
}
if((nowDate+7) > monthDays[nextMonth-1]){
nextDate = (nowDate+7)-monthDays[nextMonth-1];
nextMonth+=1;
}else{
nextDate = nowDate+7;
}
nextMonth = nextMonth<10?'0'+nextMonth:nextMonth;
nextDate = nextDate<10?'0'+nextDate:nextDate;
return `${nextYear}-${nextMonth}-${nextDate}`;
},
initDictionary(){
var vm = this;
console.log('initDictionary');

this.$http.get('/api/contracte/queryDictionary',{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
let tempData = data.data;
let tempObj = {};
let tempFieldObj = {};
for(let i=0;i<tempData.length;i++){
let itemType = tempData[i].itemType;
tempObj[itemType] = [tempData[i]];
tempFieldObj[itemType] = tempData[i].desc;
for(let j=0;j<tempData.length;j++){
if(tempData[j].itemType==itemType){
tempObj[itemType] = Array.from(new Set([...tempObj[itemType],tempData[j]]));
}
}
}
this.dictionary = Object.assign(tempObj);
this.fieldObj = tempFieldObj;
for(let obj of tempObj['3']){
this.diseaseOptions.push({key:obj.itemId,value:obj.itemName});
}
console.log(this.dictionary,this.fieldObj);
}else{
console.error(data);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.error(error);
vm.showToast = true;
vm.toastMsg = '';
})
},
watchLastMenstruation(time){
this.info.pregnancyBirth = this.getPregnancyBirth(time);

if(this.info.createdDate){
let temp = new Date(this.info.createdDate).getTime() - new Date(this.info.lastMenstruation).getTime();
if(temp<0 || isNaN(temp)){
temp=0;
}
this.info.booksGestationalWeeks = Math.round((temp)/(24*3600*1000)/7);
}
},
watchDiagnosisDate(){

if(this.info.diagnosisDate){
let temp = new Date(this.info.diagnosisDate).getTime() - new Date(this.info.lastMenstruation).getTime();
if(temp<0 || isNaN(temp)){
temp=0;
}
this.info.gestationalWeeks = Math.round((temp)/(24*3600*1000)/7);
}
},
saveInfo(){
let vm = this;
this.info.managerId = this.managerId;
this.info.doctorId = this.doctorId;

let tempObj = this.info;
for(let key in tempObj){
if(tools.isObj(tempObj[key]) || tools.isArray(tempObj[key])){
tempObj[key] = JSON.stringify(tempObj[key]);
}
}

console.log(JSON.stringify(tempObj))
this.$http.axios({method:'POST',url:'/motherhood/add',data:tempObj}).then((result) => {
result = result.data;
if(result.resultCode && result.resultCode.code=='SUCCESS'){
vm.showMask = false;
vm.showToast = true;
vm.toastMsg = '';
setTimeout(() => {
tools.next('/m/myFamilyDoctor.html?managerId='+vm.managerId+'&buildType=3');
},1500)
}else{
vm.showMask = false;
console.error(result);
vm.showToast = true;
vm.toastMsg = result.resultCode.message;
}
},(error) => {
vm.showMask = false;
console.error(error);
vm.showToast = true;
vm.toastMsg = '';
});
},
nextPageFn(num){
