this.showToast = true;
this.toastMsg = '';
return false;
}
this.saveInfo();
return ;
}
this.showBoxNum++;
this.tabSelected = num*1+1;
document.body && (document.body.scrollTop = 0);
document.documentElement && (document.documentElement.scrollTop = 0);
